  position: absolute;
  opacity: 0;
  top: 10px;
  z-index: 99;
  -webkit-transform: scale(1.5);
  transform: scale(1.5);  
  cursor: pointer;
  /*-webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;*/
}
input[type=radio]:checked::after{
  opacity: 1;
  background-color: red;
  width: 15px;
  line-height: 1px;
}
